.weatherList {
    display: grid;
    grid-template-rows: 150px 1fr;
}

.addCity {
    width: 420px;
    height: 50px;
    border-radius: 20px;
    font-size: 20px;
    padding: 15px;
    margin: 5px;
    text-align: center;
    background: rgba(60, 133, 222 .7 );
    -webkit-box-shadow: 0px 0px 25px 6px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 25px 6px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 25px 6px rgba(0,0,0,0.75);
    display: flex;
}

.addCity input {
    padding: 10px;
    font-size: 20px;
    border: 2px solid rgba(255, 255, 255, .5);
@include box-shadow(
0 2px 10px rgba(0, 0, 0, .25) inset,
0 2px 10px rgba(0, 0, 0, .25));
@include border-radius(10px);
    background: rgba(79, 195, 181, .5);
@include appearance(none);
    outline: none;
    color: honeydew;
    width: 240px;
    margin: 0 auto;
    -webkit-box-shadow: 0px 0px 32px -10px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 32px -10px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 32px -10px rgba(0,0,0,0.75);
}

.addCity input::placeholder{
    color: rgba(255, 255, 255, .9);
}

.btn {
    color: whitesmoke;
    text-transform: uppercase;
    font-size: 18px;
    margin-left: 10px;
    font-weight: 500;
    background-color:   rgba(50,100,200, 0.6);
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
    position: relative;
    border: 1px solid rgba(255,255,255,0.5);
    overflow: hidden;
    border-radius: 3px;
    height: 50px;
    width: 130px;
    text-align: center;
    cursor: pointer;
    outline: 0 !important;
    -webkit-box-shadow: 0px 0px 32px -10px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 32px -10px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 32px -10px rgba(0,0,0,0.75);
}

.btn:hover {
    background-color: rgba(255,255,255, 0.3);
}

.btn:active {
    background-color: rgba(255,255,255, 0.9);
    transition: 0.5s;
}

.btn::before, .button15::after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background-color: rgba(255,255,255,0.25);
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
    -webkit-transform: translate(0,-100%);
    transform: translate(0,-100%);
    -webkit-transition-timing-function: cubic-bezier(0.75, 0, 0.125, 1);
    transition-timing-function: cubic-bezier(0.75, 0, 0.125, 1);
}
.btn::after{
    -webkit-transition-delay: 0.2s; /* Safari */
    transition-delay: 0.2s;
}
.btn:hover::before, .button15:hover::after{
    -webkit-transform: translate(0,0);
    transform: translate(0,0);
}

.cardElements {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 300px;
}