.app-wrapper {
    max-width: 100vw;
}

.app-wrapper__page {
    display: grid;
    max-width: 100vw;
    min-height: 100vh;
    margin: 0 auto;
    padding: 40px;
    background-color: gray;
    background-image: url('https://d2v9y0dukr6mq2.cloudfront.net/video/thumbnail/D8qa-2E/blue-light-rainy-day-mood-raining-weather-background-water-drops-on-window_bflfeee7r__F0000.png');
    background-size: cover;

    background-attachment: fixed;
}


