.header {
    background-color: #171717;
    height: 80px;
    display: grid;
    grid-template-columns: 100px 1fr;
}

.header img {
    height: 80px;
    display: inline-block;
    margin-left: 30px;
    }

.name {
    color: whitesmoke;
    font-size: 25px;
    font-weight: 600;
    display: inline-block;
    width: 200px;

}